/** @format */

import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link, useHistory } from 'react-router-dom';
import '../css/LogIn.css';

//   onLogin  /      
const LogIn = ({ onLogin }) => {
    const [tempUser, setTempUser] = useState(null);
    const [id, setId] = useState('');
    const [password, setPassword] = useState('');
    const history = useHistory();
    sessionStorage.setItem('User', JSON.stringify(tempUser));

    const onChangeId = (e) => {
        setId(e.target.value);
    };

    const onChangePassword = (e) => {
        setPassword(e.target.value);
    };

    const handleClick = () => {
        if (id === tempUser.user_id && password === tempUser.password) {
            alert(`${tempUser.name} .`);
            history.push('/');
        } else {
            alert('     .');
        }
        onLogin(tempUser);
    };

    useEffect(() => {
        const fetchUsers = async (i) => {
            try {
                const response = await axios.get(
                    'https://raw.githubusercontent.com/ddidce/cleaning.github.io/master/data.json',
                );
                setTempUser(response.data.emergencyContacts[0]);

                for (i = 0; i < response.data.length; i++) {}
            } catch (error) {
                alert(',');
            }
        };
        fetchUsers();
    }, []);

    return (
        <div id="A_Container_Wrap">
            <div className="inner_txt member">
                <h3 className="sub_tit"></h3>
                <p className="sub_txt">
                          
                    .
                </p>
                <form
                    name="loginform"
                    action=""
                    method="post"
                    className="login_form"
                >
                    <input
                        type="text"
                        name="strId"
                        placeholder=""
                        onChange={onChangeId}
                        value={id}
                    />
                    <input
                        type="password"
                        name="strPw"
                        placeholder=""
                        onChange={onChangePassword}
                        value={password}
                    />
                    <button type="button" onClick={handleClick}>
                        
                    </button>
                    <ul>
                        <li className="list_after">
                            <Link to="/SignUp"></Link>
                        </li>
                        <li className="list_after2">
                            <Link to=""> </Link>
                        </li>
                        <li>
                            <Link to=""> </Link>
                        </li>
                    </ul>
                </form>
            </div>
        </div>
    );
};

export default LogIn;
