import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import Navbar from '../../components/Navbar';
import Footer from '../../components/Footer';
import ReservationConfirmInfo from './ReservationConfirmInfo';
import '../../css/ReservationConfirm.css';

const ReservationConfirm = ({ logout }) => {
    const [prodInfo, setProdInfo] = useState([
        {
            id: 1,
            service: '',
            serviceArea: ' ',
            payment: '200,000 ',
            status: '',
            management: '',
        },
        {
            id: 2,
            service: '',
            serviceArea: '  ',
            payment: '400,000 ',
            status: '',
            management: '',
        },
        {
            id: 3,
            service: '',
            serviceArea: '  ',
            payment: '150,000 ',
            status: '',
            management: '',
        },
    ]);

    const onRemove = (id) => {
        setProdInfo(prodInfo.filter((user) => user.id !== id));
    };

    const listItem = prodInfo.map((list, i) => {
        return (
            <ReservationConfirmInfo
                key={i}
                reservation={list}
                onRemove={onRemove}
            />
        );
    });
    return (
        <>
            <Navbar logout={logout} />
            <div className="sub_visual sub_visual06">
                <div className="inner">
                    <h2> </h2>
                    <ul className="sub_tab">
                        <li>
                            <Link to="/MemberModifiy"></Link>
                        </li>

                        <li className="on">
                            <Link to="/ReservationConfirm"> </Link>
                        </li>
                    </ul>
                </div>
            </div>
            <div className="order_product">
                <div className="order_title">  </div>
                <table border="0">
                    <colgroup>
                        <col style={{ width: '150px' }} />
                        <col style={{ width: '200px' }} />
                        <col style={{ width: 'auto' }} />
                        <col style={{ width: '110px' }} />
                        <col style={{ width: '130px' }} />
                        <col style={{ width: '130px' }} />
                    </colgroup>
                    <thead>
                        <tr>
                            <th scope="col">ID</th>
                            <th scope="col" colSpan="2">
                                
                            </th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                            <th scope="col"></th>
                        </tr>
                    </thead>
                    <tbody>{listItem}</tbody>
                </table>
            </div>
            <Footer />
        </>
    );
};

export default ReservationConfirm;
