import React from 'react';
import { Link } from 'react-router-dom';

const BoardInfo = ({ board }) => {
    return (
        <>
            <tr key={board.num}>
                <td>{board.readcnt}</td>
                <td className="tit">
                    {/* Link   state  pathname   
                    router   path  
                         state  */}
                    <Link
                        to={{
                            pathname: '/BoardDetail/' + board.num,
                            state: { board: board },
                        }}
                    >
                        {board.title}
                    </Link>
                </td>
                <td>{board.author}</td>
                <td>{board.writeday.substr(0, 10)}</td>
            </tr>
        </>
    );
};

export default BoardInfo;
