import React from 'react';
import { Link } from 'react-router-dom';
import '../css/Navbar.css';

const Navbar = ({ logout }) => {
    const cureentUser = JSON.parse(sessionStorage.getItem('User'));
    const onLogout = () => {
        logout();
        sessionStorage.clear();
        window.location.href = '/';
    };
    return (
        <header className="navbar">
            <nav>
                <div className="navbar-left"></div>
                <div className="navbar-right">
                    <ul>
                        <li className="logo">
                            <Link to="/"></Link>
                        </li>
                        <li className="nav-menu">
                            <Link to="/"></Link>
                        </li>
                        <li className="nav-menu">
                            <Link to="/CompanyInfo"></Link>
                        </li>
                        <li className="nav-menu">
                            <Link to="/DetailService"></Link>
                        </li>
                        <li className="nav-menu">
                            <Link to="/AboutCustom"></Link>
                        </li>
                        <li className="nav-menu">
                            <Link to="/Board"></Link>
                        </li>
                        {(() => {
                            //     if 
                            if (cureentUser) {
                                return (
                                    <div className="login_part">
                                        <li id="login" className="info">
                                            <Link
                                                to="/MemberModifiy"
                                                target="_self"
                                            >
                                                
                                            </Link>
                                        </li>
                                        <li id="login">
                                            <Link
                                                to="/"
                                                target="_self"
                                                onClick={onLogout}
                                            >
                                                
                                            </Link>
                                        </li>
                                    </div>
                                );
                            }
                            //if     >   
                            return (
                                <div className="login_part">
                                    <li id="login" className="login">
                                        <Link to="/Login" target="_self">
                                            
                                        </Link>
                                    </li>
                                    <li id="login">
                                        <Link to="/Signup" target="_self">
                                            
                                        </Link>
                                    </li>
                                </div>
                            );
                        })()}
                    </ul>
                </div>
            </nav>
        </header>
    );
};

export default Navbar;
